import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import classNames from 'classnames';
import styled from 'styled-components';
import { BREAKPOINT_LG,BLUE_COLOR,DARK_FONT_COLOR, LIGHT_BACKGROUND,BREAKPOINT_MDS,BREAKPOINT_MDL,FLEX_MDL,FLEX_MDS,HEADER_COLOR,SELECTED_COLOR,ICON_BASE_LG,FONT_COLOR,FLEX,ICON_BASE,ICON_BASE_MD,FLEX_LG,FLEX_SM,FLEX_MD,ICON_SIZE,ICON_SIZE_MD,SECONDARY_FONT_COLOR,ICON_NAME_COLOR,DARK_BORDER_COLOR,FADE_FONT_COLOR, BREAKPOINT_MD, BREAKPOINT_SM} from '../types';
import { changeSideIcon, setSelectedIcon } from '../../actions/iconActions';
const IconBox = styled.div`
transition: background-color .4s, box-shadow 0.15s;
    animation-fill-mode: forwards;


    .new-dot{
      background: ${BLUE_COLOR};
    width: 5px;
    height: 5px;
    border-radius: 50%;
    position: absolute;
    right: 5px;
    top: 5px;
    }
            i {
              color: ${props => props.dark ? DARK_FONT_COLOR : FONT_COLOR};
           
                font-size: ${ICON_SIZE};  
                 @media screen and (max-width: ${BREAKPOINT_LG}) {
                font-size: ${ICON_SIZE}; 
              }
              
              
                @media screen and (max-width: ${BREAKPOINT_MD}) {
                font-size: ${ICON_SIZE_MD}; 
              }
              }
              &.fade{
                &.active{
                  .icon-base {
                opacity:1;
              }
                }
                .icon-base {
                opacity:0.5;
              }
              }
              &.active{
                i {
                  color: ${props => props.dark ? '#FFF' : SELECTED_COLOR};
  
              }
              }
            cursor: pointer;
            outline: none !important;
           display:inline-block;

            
                    
            
            
             &:hover{
                 z-index: 9999999;
    }  
   
    border-radius: 2px;overflow: hidden;
               overflow: hidden;
              

   
            .icon-box-inner {
            
              margin: 2px;
              border: 2px solid transparent;
              font-weight: 700;
              position: relative;
              font-size: 16px;
              border-radius: 5px;
              line-height: 35px;
              text-align: center;
              pointer-events: none;
              .icon-base{
                height:${ICON_BASE};
               
                i{
                  line-height:${ICON_BASE};
                }
                @media screen and (max-width: ${BREAKPOINT_LG}) {
                  height:${ICON_BASE_LG};
                i{
                  line-height:${ICON_BASE_LG};
                }
              }
                @media screen and (max-width: ${BREAKPOINT_MD}) {
                  height:${ICON_BASE_MD};
                i{
                  line-height:${ICON_BASE_MD};
                }
              }

              }
              .icon-box-name {
                  font-size: 12px;
                  -webkit-touch-callout: none;
    -webkit-user-select: none; 
    text-transform:capitalize;
     -khtml-user-select: none; 
       -moz-user-select: none; 
        -ms-user-select: none; 
            user-select: none;
                  font-weight: 400;
                  color: ${ICON_NAME_COLOR};
                  @media screen and (max-width: ${BREAKPOINT_LG}) {
                display:none;
              }
                  margin: 0px 10px;
                  text-overflow: ellipsis;
                  overflow: hidden;
                  white-space: nowrap;
                  line-height: 20px;    margin-top: 5px; margin-bottom: 16px;
              }
              padding: 15px;
             @media screen and (max-width: ${BREAKPOINT_LG}) {
                width: auto;
                padding: 15px ;
              }
              @media screen and (max-width: ${BREAKPOINT_MD}) {
                padding: 10px;
              }

            }
            &:active {
              box-shadow: none;
    border-radius: 5px;
    background: #f5f5f5;
}
&:hover {
    box-shadow: 0 0 10px rgba(0,0,0,0.12);
    border-radius: 5px;
    background: #fff;
}
            &.active{
              box-shadow: none;
    border-radius: 5px;
    background: #f5f5f5;
}
@media screen and (max-width: ${BREAKPOINT_MDL}) {
  cursor: initial;
  &.active,&:hover,&:active { box-shadow: none;background:transparent;}     
              }
        `;
class IconsListItem extends Component {
    constructor(props) {
        super(props);
        this.handleBoxClick = this.handleBoxClick.bind(this);
        this.state = {
            selectedIcon: {
                name: null,
                slug: null,
                type_of_icon:null,
            },
            searchTerm: '',

        };
    }

    handleBoxClick(name, slug, type_of_icon) {
        this.props.changeSideIcon(true);
        this.props.setSelectedIcon(name, slug, type_of_icon);

    }
    componentWillReceiveProps(newProps) {
      
        this.setState({
            selectedIcon: newProps.selectedIcon
        });
        this.setState({
            searchTerm: newProps.searchTerm
        });
       
        

    }
    render() {
        
        var boxClass = classNames({
            'active': (this.state.selectedIcon.slug === this.props.icon.slug),
            'icon-box ':true,
        });
        return (
            <IconBox dark={this.props.dark} className={boxClass} ref="iconbox" tabIndex={this.props.num} onFocus={() => this.handleBoxClick(this.props.icon.name, this.props.icon.slug, this.props.icon.type_of_icon)}>
  
      <div className="icon-box-inner" >
      {this.props.icon.new&&<div className="new-dot"></div>}
      <div className="icon-base" >
      {this.props.icon.type_of_icon==='REGULAR' && <i className={"fadeIn animated bx bx-"+this.props.icon.name}></i>}
      {this.props.icon.type_of_icon==='SOLID' && <i className={"fadeIn animated bx bxs-"+this.props.icon.name}></i>}
 {this.props.icon.type_of_icon==='LOGO' && <i className={"fadeIn animated bx bxl-"+this.props.icon.name}></i>}
</div>

     </div></IconBox>
        );
    }
}
;

function mapStateToProps(state) {
    return {
        selectedIcon: state.selectedIcon,
        searchTerm: state.searchTerm,
    };
}
function mapDispatchToProps(dispatch) {
    return bindActionCreators({
        changeSideIcon,
        setSelectedIcon
    }, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(IconsListItem);
